<template>
  <div>
    <div class="text-center" v-if="$apollo.loading">
      <v-progress-circular :value="20"></v-progress-circular>
    </div>
    <div v-else class="page-wrap">
      <Breadcrumbs />
      <div v-if="page.page_blocks">
        <div v-for="block in page.page_blocks" :key="block.id">
          <v-container v-if="block.item.block_layout === 'three_column'">
            <v-row>
              <v-col cols="12" sm="4">
                <div v-for="blockItem in block.item.block_content_column_one.blocks" :key="blockItem.id">
                  <keep-alive>
                    <component :is="pageBlock(blockItem.type)" :block="blockItem" :key="blockItem.id" class="block-component"></component>
                  </keep-alive>
                </div>
              </v-col>
              <v-col cols="12" sm="4">
                <div v-for="blockItem in block.item.block_content_column_two.blocks" :key="blockItem.id">
                  <keep-alive>
                    <component :is="pageBlock(blockItem.type)" :block="blockItem" :key="blockItem.id" class="block-component"></component>
                  </keep-alive>
                </div>
              </v-col>
              <v-col cols="12" sm="4">
                <div v-for="blockItem in block.item.block_content_column_three.blocks" :key="blockItem.id">
                  <keep-alive>
                    <component :is="pageBlock(blockItem.type)" :block="blockItem" :key="blockItem.id" class="block-component"></component>
                  </keep-alive>
                </div>
              </v-col>
            </v-row>
          </v-container>
          <v-container v-if="block.item.block_layout === 'two_column'">
            <v-row>
              <v-col cols="12" sm="6">
                <div v-for="blockItem in block.item.block_content_column_one.blocks" :key="blockItem.id">
                  <keep-alive>
                    <component :is="pageBlock(blockItem.type)" :block="blockItem" :key="blockItem.id" class="block-component"></component>
                  </keep-alive>
                </div>
              </v-col>
              <v-col cols="12" sm="6">
                <div v-for="blockItem in block.item.block_content_column_two.blocks" :key="blockItem.id">
                  <keep-alive>
                    <component :is="pageBlock(blockItem.type)" :block="blockItem" :key="blockItem.id" class="block-component"></component>
                  </keep-alive>
                </div>
              </v-col>
            </v-row>
          </v-container>
          <v-container v-if="block.item.block_layout === 'one_column' && block.item.block_type === 'content_block'">
            <v-row>
              <v-col cols="12" sm="12">
                <div v-for="blockItem in block.item.block_content_column_one.blocks" :key="blockItem.id">
                  <keep-alive>
                    <component :is="pageBlock(blockItem.type)" :block="blockItem" :key="blockItem.id" class="block-component"></component>
                  </keep-alive>
                </div>
              </v-col>
            </v-row>
          </v-container>
        </div>
      </div>
      <!-- Dynamic components yo -- https://vuejs.org/v2/guide/components-dynamic-async.html -->
      <!-- <div v-if="page.page_builder">
        <div v-for="block in page.page_builder.blocks" :key="block.id">
          <keep-alive>
            <component :is="pageBlock(block.type)" :block="block" :key="block.id" class="block-component"></component>
          </keep-alive>
        </div>
      </div> -->
    </div>
  </div>
</template>

<script>
import { PAGES_QUERY, PAGES_BY_ID_QUERY } from '@/graphql/queries'
const Breadcrumbs = () => import(/* webpackChunkName: "Breadcrumbs" */ '@/components/sections/Breadcrumbs')
const TextBlock = () => import(/* webpackChunkName: "TextBlock" */ '@/components/blocks/TextBlock')
const TabsBlock = () => import(/* webpackChunkName: "TabsBlock" */ '@/components/blocks/TabsBlock')
const ListBlock = () => import(/* webpackChunkName: "ListBlock" */ '@/components/blocks/ListBlock')
const TableBlock = () => import(/* webpackChunkName: "TableBlock" */ '@/components/blocks/TableBlock')
const CodeBlock = () => import(/* webpackChunkName: "CodeBlock" */ '@/components/blocks/CodeBlock')
const ImageBlock = () => import(/* webpackChunkName: "ImageBlock" */ '@/components/blocks/ImageBlock')

// const OneColumnBlock = () => import(/* webpackChunkName: "ThreeColumnBlock" */ '@/components/blocks/OneColumnBlock')
// const TwoColumnBlock = () => import(/* webpackChunkName: "ThreeColumnBlock" */ '@/components/blocks/TwoColumnBlock')
// const ThreeColumnBlock = () => import(/* webpackChunkName: "ThreeColumnBlock" */ '@/components/blocks/ThreeColumnBlock')

export default {
  name: 'Page',
  metaInfo () {
    return {
      title: this.metaTitle || this.pageTitle,
      meta: [
        { name: 'description', content: this.metaDescription },
        { property: 'og:title', content: this.metaTitle },
        { property: 'og:site_name', content: 'Office of Natural Resources Revenue' },
        { property: 'og:type', content: 'website' },
        { name: 'robots', content: 'index,follow' }
      ]
    }
  },
  components: {
    Breadcrumbs,
    TextBlock,
    TabsBlock,
    ListBlock,
    TableBlock,
    CodeBlock,
    ImageBlock,
    // OneColumnBlock,
    // TwoColumnBlock,
    // ThreeColumnBlock
  },
  data() {
    return {
      pages: [],
      pages_by_id: [],
      code: '',
      colCount: 1
    }
  },
  apollo: {
    pages: {
      query: PAGES_QUERY,
      loadingKey: 'loading...',
    },
    pages_by_id: {
      query: PAGES_BY_ID_QUERY,
      loadingKey: 'loading...',
      variables () {
        return {
          ID: this.findPageBySlug.id
        }
      },
      // fetchPolicy: 'cache-and-network'
    }
  },
  props: {
    slug: String,
  },
  created () {
    this.$apollo.queries.pages_by_id.refetch()
  },
  methods: {
    pageBlock: function(type) { 
      let block
      switch (type) {
        case 'header':
        case 'paragraph':
          block = TextBlock
          break
        case 'tabs':
          block = TabsBlock
          break
        case 'list':
          block = ListBlock 
          break
        case 'table':
          block = TableBlock 
          break
        case 'code':
          block = CodeBlock
          break
        case 'image':
          block = ImageBlock 
          break
        default:
          block = TextBlock
          break
      }
      return block
    },
  },
  computed: {
    findPageBySlug () {
      const str = this.$route.path
      const route = str.replace(/\//g, '')
      let page
      if(this.pages) {
        page = this.slug ? this.pages.find(page => page.slug === this.slug) : this.pages.find(page => page.slug === route)
      }

      return page
    },
    page () {
      return this.pages_by_id
    },
    metaTitle () {
      return this.pages_by_id.meta_title
    },
    metaDescription () {
      return this.pages_by_id.meta_description
    },
    pageTitle () {
      return this.pages_by_id.title
    }
  }
}
</script>

<style lang="scss" scoped>
.page-wrap {
  padding-top: 25px;
}

.block-component {
  margin: 24px 0;
}
</style>